*{ margin: 0px; padding: 0px; list-style: none; outline: 0px; box-sizing: border-box;}
body,html{ margin: 0px; padding: 0px; font-family: 'Open Sans', sans-serif; font-size: 15px; color: #000000; }

.headerDiv{ display: flex; width: 100%; align-items: center; justify-content: center; padding: 12px 0; transition: all 0.5s ease-in-out 0s; position: fixed; top:0px; background: #fff; z-index: 2;} 
.headerDiv.scroll{ position: fixed; left: 0px;top:0px; transition: all 0.5s ease-in-out 0s; border-bottom: 1px solid #ddd;}
.logo{ float: left; }

.bannerDiv{ display: flex;  width: 100%; height: auto; margin: 50px 0 0 0; position: relative;} 
.bannerDiv img{ width: 100%; height: auto;}
.container{ width: 1440px; margin: auto;} 
.bannerDivIn{ width: 100%; display: flex; position: absolute; left: 0px; top:0px; bottom: 0px; height: auto;}
.bannerDivIn h1{ font-size: 44px; color: #fff; line-height: 50px; padding: 0 0 20px 0;}
.bannerDivIn p{ font-size: 22px; color: #fff; padding: 0 0 22px 0;}

.enroolNow{ border: 1px solid #fff; width: 150px; height: 40px; font-size: 16px; color: #fff; display: flex; align-items: center; justify-content: center; text-decoration: none;}
.enroolNow:hover{ background: #fff; color: #333;}

.bannerDivIn aside{ width: 580px;}

.solutionDiv{ display: flex; width: 100%; padding: 70px 0 40px 0; background: #fafafa;} 
.solutionDiv h2{ font-size: 34px; color: #00bcf2; text-align: center; padding: 0 0 40px 0; }
.solutionWrap{ display: flex; width: 100%;}
.loopDiv{ width: 364px; float: left; padding: 0 24px 0 0;} 
.loopDiv h3{ font-size: 28px; font-weight: 600; padding:15px 0;}
.loopDiv ul{ display: block; width: 100%; }
.loopDiv ul li{ padding: 0 0 10px 15px; position: relative;}
.loopDiv ul li:after{ content: ""; position: absolute; left:0px; top:7px; width: 5px; height: 5px; background: #000; border-radius: 50%; }
.loopDiv ul li a{ color: #000; text-decoration: none;}
.loopDiv ul li a:hover{ text-decoration: underline;}
.loopDiv:nth-child(4n){ padding-right: 0px;}
.loopDiv img{ max-width: 100%; height: auto;}

.tabbingDiv{ display: flex; width: 100%; padding: 90px 0 70px 0; border-bottom: 1px solid #d9d9d9; }
.tabbingDivIn{ width: 100%; display: flex;  align-items: center; justify-content: center; padding: 0 0 90px 0; }
.tabbingDivIn ul{ display: flex; align-items: center; justify-content: center; }
.tabbingDivIn ul li{ font-size: 22px; color: #666666; margin: 0 27px; cursor: pointer; position: relative;}
.tabbingDivIn ul li.active{ color: #333333;}
.tabbingDivIn ul li.active:after{ width: 100%;}
.tabbingDivIn ul li:hover{ color: #333333;}
.tabbingDivIn ul li:after{ content: ""; position: absolute; left:0px; bottom:-8px; width: 0%; height: 3px; background: #0067b8; transition: all 0.5s ease-in-out 0s;}
.tabbingDivIn ul li:hover:after{ width: 100%; transition: all 0.5s ease-in-out 0s;}

.tabbingDivContent{ width: 100%; display: block}
.tabbingDivContent h2{ font-size: 34px; color: #00bcf2; text-align: center; padding: 0 0 45px 0; }
.loopingtabbing{ float: left; width: 100%; margin: 0 0 35px 0;} 
/*.loopingtabbing img{ float: right;}*/
.loopingtabbing aside{ float: left; width: 578px; padding: 120px 0 0 0;  }
.loopingtabbing aside h3{ font-size: 28px; padding: 0 0 15px 0;}
.loopingtabbing aside ul{ display: block;}
.loopingtabbing aside ul li{ display: block; position: relative; padding: 0 0 10px 20px;}
.loopingtabbing aside ul li::after{ content: ""; position: absolute; left:0px; top:7px; background: #000; border-radius: 50%; width: 5px; height: 5px;}
.bestdiv{ margin: 0 0 0px 0;}

.bestdiv{ float: right; }
.loopingtabbing aside img{ margin: 0 0 15px 0;}
.rightIn{ background: #fafafa;}
.rightIn .bestdiv{ float: left; }
.rightIn aside{ float: right;}

.instituteDiv{display: flex; width: 100%; padding: 90px 0 127px 0;}
.instituteDiv h2{ font-size: 34px; text-align: center; color: #00bcf2; padding: 0 0 30px 0;}
.instituteDiv ul {float: left; width: 100%; display: flex; align-items: center; justify-content: center;}
.instituteDiv ul li{ float: left; width: 205px; height: 199px; box-shadow: 0 0 10px #ccc; margin: 0 12px;}
.instituteDiv ul li label { float: left; width: 100%; height: 100%; display: flex; align-items: center;justify-content: center;}

.footerDiv{ display: block; width: 100%; padding: 14px 0; background:#666666; color: #fff; text-align: center; font-size: 18px; text-align: center;}
.footerDiv b{ font-weight: 400;}
.footerDiv p { width: 440px; margin: auto; font-weight: 300;}

.coverTab{ float: left; width: 100%; display: none;}

.smootScroll{ position: fixed; bottom: 0px; right: 0px; cursor: pointer;}

.navidiv{ float:right;}
.navidiv li{ float: left; margin:0 20px;}
.navidiv li a{ color: #000; text-decoration: none;}

.counter{ background: #eee; width: 100%; padding: 30px 0;}
.todo{ width: 100%; float: left; background: #00bcf2; padding: 20px;}

/**************************************/

@media(max-width:1440px){
    .container{ width: 100%; padding: 0 20px;}
    .loopDiv{ width: 25%;}
    .loopingtabbing aside{ width: 46%;}
    .bestdiv{ max-width: 50%;}
    .loopingtabbing aside{ padding: 80px 0 20px 20px;}
}


@media(max-width:1200px){
    .loopingtabbing aside{ padding: 20px 20px 20px 20px; width: 49%;}
    .loopingtabbing aside h3, .loopDiv h3{ font-size: 20px;}
    .smootScroll img{ max-width: 30px; margin-right: 10px;}
    .bannerDivIn h1{ font-size: 34px; line-height: 40px; padding-bottom: 5px;}
    .bannerDivIn p{ font-size: 16px;}
    .tabbingDivContent h2, .instituteDiv h2, .solutionDiv h2{ font-size: 25px;}
    .solutionDiv{ padding-top: 40px;}
    .loopDiv ul li{ font-size: 13px;}    
    .tabbingDiv{ padding-top: 50px; padding-bottom: 10px;}
    .tabbingDivIn{ padding-bottom: 50px;}
    .instituteDiv{ padding-top: 40px; padding-bottom: 40px;}
}

@media(max-width:900px){
    .bannerDivIn h1{ font-size: 25px; line-height: 30px;}
    .loopingtabbing aside h3, .loopDiv h3{ font-size: 14px;}
    .loopDiv{ width: 48%; padding: 0px!important; margin: 1%;}
    .loopDiv img{ width: 100%;}
    .solutionWrap{ flex-wrap: wrap;}
    .loopingtabbing aside{ width: 100%;}
    .bestdiv{ width: 100%; max-width: 100%;}
    .instituteDiv ul{ flex-wrap: wrap;}
    .instituteDiv ul li{ margin-bottom: 20px;}
    .bannerDivIn aside{ width: 390px;}
}

@media(max-width:600px){
    .bannerDivIn h1{ font-size: 20px; line-height: 24px;}
    .bannerDivIn aside{ width: 50%;}
    .bannerDivIn p{ font-size: 14px;}
    .enroolNow {  border: 1px solid #fff; width: 100px; height: 26px; font-size: 12px;color: #fff; }
    .instituteDiv ul li{ width: 165px; height: 159px; margin: 5px;}
    .footerDiv p{ width: 100%; font-size: 14px; padding: 0 20px;}
}

@media(max-width:490px){
 .bannerDivIn h1 {font-size: 14px;line-height: 16px;}
 .bannerDivIn p{ font-size: 12px; padding-bottom: 5px;}
 .tabbingDivContent h2, .instituteDiv h2, .solutionDiv h2 {font-size: 20px;}
 .loopDiv{ width: 98%; margin-bottom: 25px;}
 .instituteDiv ul li {width: 105px;height: 99px;margin: 10px;}
 .instituteDiv ul li img{ max-width: 90%; height: auto;}
 .tabbingDivIn ul li{ font-size: 16px;}
}

@media(max-width:400px){
    .bannerDivIn p{ display: none}
    .bannerDivIn h1{ padding-bottom: 10px;}
}
